    <!-- 遮罩层 -->
    <div class="mask" v-if="maskIsShow">
      <div class="mask_box">
        <div class="title">
          <h1>新建待办</h1>
          <span @click="updateMessage(1)">确定</span>
          <span @click="updateMessage(0)">取消</span>
        </div>
        <textarea placeholder="想做点什么?" v-model="msg"></textarea>
        <div class="mask_bottom">
          <i class="text">选择分类 :</i>
          <ul>
            <div class="box">
              <li>
                <span @click="changeColor('235, 61, 60', 0)"><i></i></span>
              </li>
              <li>
                <span @click="changeColor('248, 103, 22', 1)"><i></i></span>
              </li>
              <li>
                <span @click="changeColor('51, 118, 247', 2)"><i></i></span>
              </li>
              <li>
                <span @click="changeColor('17, 175, 116', 3)"><i></i></span>
              </li>
            </div>
          </ul>

          <span
            class="iconfont icon-jishiben"
            :class="{
              emergency: this.priority == 0,
              important: this.priority == 1,
              priority: this.priority == 2,
              common: this.priority == 3,
            }"
          ></span>
        </div>
      </div>
