</template>

<script>
// 引入子组件
import SearchList from "./components/SearchList.vue";
import LoginRegMask from "./components/LoginRegMask.vue";
import userMsg from "@/components/userMsg.vue"
import { mapState } from "vuex";
import { nanoid } from "nanoid";

export default {
  name: "App",
  data() {
    return {
      nowdater: undefined,
      msg: "",
      priority: 0,
      isShow: 0,
      isRouterActive:true
    };
  },
    provide(){
    return {
      reload:this.reload
    }
  },
  components: {
    SearchList,
    LoginRegMask,
    userMsg
  },
  mounted() {
    // 初始化基本信息
    this.$store.dispatch("initMsg");
    // 禁止文本选中
    document.onselectstart = function () {
      return false;
    };
    // 接收dater数据
    this.$bus.$on("getNowDater", this.getNowDater);
    this.$bus.$on("alertMsg",this.createLi)
  },

  methods: {
    // 全局事件总线获取当前dater
    getNowDater(date) {
      this.nowdater = date;
    },

    // 创建待办  派发到vuex处理
    updateMessage(type) {
      // 当提交时,对数据进行处理
      if (type) {
        if (this.msg != "") {
          let obj = {
            id: nanoid(),
            dater: this.nowdater,
            msg: this.msg,
            priority: this.priority,
            isChecked: false,
          };
          this.msg = "";
          this.$store.dispatch("createToDoList", obj);
          this.$store.dispatch("changeMaskShow", false);
        } else {
          this.createLi("当前无内容哦", 0);
        }
      } else {
        this.$store.dispatch("changeMaskShow", false);
      }
    },
    // 改变创建待办 右下字体图标样式
    changeColor(color, priority) {
      let icon = document.querySelector(".icon-jishiben");
      icon.style.color = `rgb(${color})`;
      this.priority = priority;
    },
    // 改变路由选中
    changeShow(num) {
      this.isShow = num;
    },
    // 创建一个提示li
    createLi(val, type = 1) {
      let li = document.createElement("li");
      li.innerText = `${val}`;
      li.style.opacity = 1;
      if (!type) {
        li.style.backgroundColor = "red";
      }
      this.$refs.alertMsg.appendChild(li);
      setTimeout(() => {
        let set =  setInterval(() => {
          li.style.opacity -= 0.05;
          if (li.style.opacity <= 0) {
            this.$refs.alertMsg.removeChild(li);
            clearInterval(set)
          }
        }, 50);
      }, 1500);
    },
    // 重载页面
    reload(){
      this.isRouterActive = false
      this.$nextTick(()=>{
        this.isRouterActive = true
      }) 
    }
  },
  computed: {
    // 遍历数据
    ...mapState(["maskIsShow", "username"]),
  },
};
</script>

