import Vue from "vue"
import Vuex from "vuex"
// 引入api
import { reqInitUserMessage } from "@/api/index"

Vue.use(Vuex)

export default new Vuex.Store({
    state: {

        // 控制创建遮罩层显隐
        maskIsShow: false,
        // 控制登录遮罩层显隐
        lgoRegMaskIsShow: false,
        // 存储内容
        todolist: JSON.parse(localStorage.getItem('TODOLIST')) || [],
        // token
        TOKEN: localStorage.getItem('TOKEN') || '',
        username: 'admin',
        nowday: localStorage.getItem('nowDay') || '',
    },
    actions: {
        // 获取用户信息
        async initMsg({ commit }) {
            // 发送ajax请求
            try {
                let res = await reqInitUserMessage()
                // 成功执行
                if (res.status == 200 && res.data.status == 0) {
                    // 交给state
                    if (res.data.data.nickname) {
                        commit('INITMSG', res.data.data.nickname)

                    } else {
                        commit('INITMSG', res.data.data.username)
                    }

                }
            } catch (error) {
                console.log(error);
            }
        },

        // 改变创建遮罩层
        changeMaskShow(context, result) {
            context.commit('CHANGEMASKSHOW', result)
        },

        // 改变登录遮罩层
        changelogMaskShow({ commit }, result) {
            commit('CHANGELOGMASKSHOW', result)
        },

        // 更新列表
        updateToDoList(context) {
            context.commit('UPDATETODOLIST')
        },

        // 新建列表
        createToDoList(context, obj) {
            let arr = JSON.parse(localStorage.getItem('TODOLIST')) || []
            arr.unshift(obj)
            localStorage.setItem('TODOLIST', JSON.stringify(arr))
            context.dispatch('updateToDoList')
        },

        // 改变选中状态
        changeChecked(context, id) {
            let arr = JSON.parse(localStorage.getItem('TODOLIST')) || []
            arr.forEach(item => {
                if (item.id == id) {
                    item.isChecked = !item.isChecked
                }
                localStorage.setItem('TODOLIST', JSON.stringify(arr))
                context.dispatch('updateToDoList')
            });
        },
        // 移除单个数据
        removeList(context, id) {
            let arr = JSON.parse(localStorage.getItem('TODOLIST')) || []
            arr = arr.filter(item => {
                return item.id != id
            })
            localStorage.setItem('TODOLIST', JSON.stringify(arr))
            context.dispatch('updateToDoList')
        },
        // 存储token到本地并传入state
        storageToken({ commit }, token) {
            localStorage.setItem('TOKEN', token)
            commit('STORAGETOKEN', token)
        },

    },
    mutations: {
        // 改变显隐数据
        CHANGEMASKSHOW(state, result) {
            state.maskIsShow = result
        },
        UPDATETODOLIST(state) {
            state.todolist = JSON.parse(localStorage.getItem("TODOLIST"))
        },
        // 存储token到state里
        STORAGETOKEN(state, token) {
            // 不能将获取到的存储进去 要通过本地存储去读
            this.state.TOKEN = localStorage.getItem('TOKEN')
        },
        // 改变log遮罩层 
        CHANGELOGMASKSHOW(state, res) {
            state.lgoRegMaskIsShow = res
        },
        // 存储用户名
        INITMSG(state, data) {
            state.username = data
        },
        //存储初始当前天
        STORAGENOWDAY(state, day) {
            localStorage.setItem('nowDay', day)
            state.nowday = localStorage.getItem('nowDay')

        },
    },
    getters: {

    }
})