import Vue from "vue"
import VueRouter from "vue-router"
Vue.use(VueRouter)


//重写$router里的push replace  当前的push 和 repalce 多次点击会报错 因为跳转路由会传(promise)之后没有promise就报错
let copePush = VueRouter.prototype.push;
let copeReplace = VueRouter.prototype.replace;

// 当调用push时 调用该函数 当传入promise 的 res rej 时正常执行 若无传入两个空箭头函数 
VueRouter.prototype.push = function (val, res, rej) {
    if (res && rej) {
        copePush.call(this, val, res, rej)
    } else {
        copePush.call(this, val, () => { }, () => { })
    }
}

// 当调用replace时 调用该函数 当传入promise 的 res rej 时正常执行 若无传入两个空箭头函数 
VueRouter.prototype.replace = function (val, res, rej) {
    if (res && rej) {
        copeReplace.call(this, val, res, rej)
    } else {
        copeReplace.call(this, val, () => { }, () => { })
    }
}

let router =  new VueRouter({
    routes:[
        {
            path:'/index',
            component:()=>import('@/pages/ToDoIndex.vue'),
            children:[
                {
                    path:'list',
                    component:()=>import('@/pages/OrderList.vue'),
                },
                {
                    path:"/",
                    redirect:"/index/list"
                }
            ]
        },
        {
            name:'statistical',
            path:'/statistical',
            component:()=>import('@/pages/DataStatistical.vue')
        },
        {
            name:'setting',
            path:'/setting',
            component:()=>import('@/pages/MySetting.vue')
        },
        {
            name:'about',
            path:'/about',
            component:()=>import('@/pages/AboutMe.vue')
        },
        {
            path:'/',
            redirect:'/index'
        }
    ],
    // 滚动到顶部
    scrollBehavior() {
        // 由vue提供的  始终滚动到顶部
        // vue3 用top   vue2  用y
        return { y: 0 }
    },
})

export default router