<template>
  <div class="diagram">
    <canvas id="canvas" width="200" height="200"
      >您的浏览器不支持 HTML5 canvas 标签。</canvas
    >
    <h1>
      今日共<span>{{ all }}</span
      >项待办,已完成<span>{{ nowNumber }}</span
      >项
    </h1>
    <img src="../../public/images/pic.png" />
  </div>
</template>

<script>
import { mapState } from "vuex";

export default {
  name: "CircularDiagram",

  data() {
    return {
      //   canvas元素
      c: null,
      //   获取上下文
      ctx: null,
      precent: 1,
      num: 0.07,
      all: undefined,
      nowdater: undefined,
      nowNumber: undefined,
    };
  },
  // 获取当前列表个数
  computed: {
    ...mapState(["todolist"]),
    nowList() {
      return this.todolist.filter((item) => {
        return item.dater == this.nowdater;
      });
    },
  },
  mounted() {
    this.c = document.getElementById("canvas");
    this.ctx = this.c.getContext("2d");
    this.$bus.$on("getcanvasDater", this.getcanvasDater);
    this.drawTrack();
    this.drawProgress(this.num);
    this.draw();
  },

  methods: {
    //绘制轨道
    drawTrack: function () {
      // 存储上下文
      this.ctx.save();
      // 开始绘制
      this.ctx.beginPath();
      // 规定线段末端为什么形状 圆形
      this.ctx.lineCap = "round";
      // 设置线段宽度
      this.ctx.lineWidth = 4;
      // 设置轮廓线段颜色 淡灰色
      this.ctx.strokeStyle = "#e5f1fa";
      // 画圆轮廓  前两个为圆心坐标(150,150)  半径100   起始度数  结束度数
      this.ctx.arc(100, 100, 95, 0, 2 * Math.PI);
      // 开始绘制
      this.ctx.stroke();
      // 结束绘制
      this.ctx.closePath();
      // 将各种颜色初始化
      this.ctx.restore();
    },
    drawTrack2: function () {
      // 存储上下文
      this.ctx.save();
      // 开始绘制
      this.ctx.beginPath();
      // 规定线段末端为什么形状 圆形
      this.ctx.lineCap = "round";
      // 设置线段宽度
      this.ctx.lineWidth = 4;
      // 设置轮廓线段颜色 淡灰色
      this.ctx.strokeStyle = "rgba(27,167,132,.1)";
      // 画圆轮廓  前两个为圆心坐标(150,150)  半径100   起始度数  结束度数
      this.ctx.arc(100, 100, 82, 0, 2 * Math.PI);
      // 开始绘制
      this.ctx.stroke();
      // 结束绘制
      this.ctx.closePath();
      // 将各种颜色初始化
      this.ctx.restore();
    },

    //绘制进度环
    drawProgress: function (num) {
      this.ctx.save();
      this.ctx.beginPath();
      this.ctx.lineCap = "round";
      this.ctx.lineWidth = 5;
      this.ctx.strokeStyle = "#2ba0fb";
      // context.arc(x,y,r,sAngle,eAngle,counterclockwise);   //x坐标,y坐标,半径,起始角,结束角,顺时针/逆时针
      this.ctx.arc(
        100,
        100,
        95,
        -Math.PI / 2,
        -Math.PI / 2 + ((2 * num) / 100) * Math.PI
      );
      this.ctx.stroke();
      this.ctx.closePath();
      this.ctx.restore();
    },

    drawProgress2: function (num) {
      this.ctx.save();
      this.ctx.beginPath();
      this.ctx.lineCap = "round";
      this.ctx.lineWidth = 5;
      this.ctx.strokeStyle = "rgb(27,167,132)";
      // context.arc(x,y,r,sAngle,eAngle,counterclockwise);   //x坐标,y坐标,半径,起始角,结束角,顺时针/逆时针
      this.ctx.arc(
        100,
        100,
        82,
        -Math.PI / 2,
        -Math.PI / 2 + ((2 * num) / 100) * Math.PI
      );
      this.ctx.stroke();
      this.ctx.closePath();
      this.ctx.restore();
    },
    //动画
    draw: function () {
      this.num += 1;
      if (this.num < this.precent)
        window.requestAnimationFrame(() =>
          setTimeout(() => {
            this.draw();
          }, 10)
        );
      else {
        this.num = this.precent;
      }
      this.ctx.clearRect(0, 0, 300, 300);
      this.drawTrack();
      this.drawProgress(this.num);
    },
    getcanvasDater(dater) {
      this.nowdater = dater;
    },
  },
  watch: {
    precent() {
      this.draw();
    },

    nowList: {
      immediate: true,
      handler() {
        // 计算precent值
        let all = this.nowList.length;
        this.all = all;
        let arr = this.nowList.filter((item) => {
          return item.isChecked;
        });
        let nowNumber = arr.length;
        this.nowNumber = nowNumber;

        this.precent = Math.floor((nowNumber / all) * 100);
      },
    },
  },
};
</script>

<style lang="less" scoped>
.diagram {
  position: relative;
  margin-top: 5px;
  padding: 10px 0;
  text-align: center;
  background-color: #fff;
  border-radius: 5%;
  h1 {
    cursor: default;
    margin-top: 5px;
    font-size: 16px;
    line-height: 1.5em;

    span {
      color: #3476f4;
    }
  }
  img {
    position: absolute;
    top: 22%;
    left: 50%;
    transform: translateX(-50%);
    width: 100px;
  }
}
</style>