<template>
  <div class="login-mask" v-if="lgoRegMaskIsShow">
    <div class="mask-box">
      <div class="pic">
        <img src="../../public/images/bg-01.jpg" alt="" />
      </div>
      <div class="login-body" v-if="logRegIsShow">
        <h1>登录<span @click="changeLogRegMask">返回</span></h1>
        <ul>
          <li>
            <span>用<em></em>户<em></em>名</span>
            <input
              type="text"
              placeholder="请输入用户名"
              v-model="username"
              @input="testUserName(0)"
              ref="username"
            />
            <p>用户名只能为字母或数字哦</p>
          </li>
          <li>
            <span>密<em></em><em></em><em></em><em></em>码</span>
            <input
              type="password"
              placeholder="请输入密码"
              v-model="password"
              @input="testPassword(0)"
              ref="password"
            />
            <p>密码必须为6-18位且包含字母和数字哦</p>
          </li>
        </ul>
        <button @click="getLoginMsg" ref="btn">立即登录</button>
        <p>还没有账号嘛~快去<em @click="changelogRegIsShow">注册</em>吧</p>
      </div>
      <div class="register-body" v-if="!logRegIsShow">
        <h1>注册<span @click="changeLogRegMask">返回</span></h1>
        <ul>
          <li>
            <span>用<em></em>户<em></em>名</span>
            <input
              type="text"
              placeholder="请输入用户名"
              v-model="username"
              @input="testUserName(1)"
              ref="username"
            />
            <p>用户名只能为字母或数字哦</p>
          </li>
          <li>
            <span>设置密码</span>
            <input
              type="password"
              placeholder="请输入密码"
              v-model="password"
              @input="testPassword(1)"
              ref="password"
            />
            <p>密码必须为6-18位且包含字母和数字哦</p>
          </li>
          <li>
            <span>重复密码</span>
            <input
              type="password"
              placeholder="请再次输入密码"
              @input="testPassword1"
              ref="password1"
            />
            <p>密码和上次不一样哦,检查一下吧~</p>
          </li>
        </ul>
        <button ref="btn" @click="getRegister">立即注册</button>
        <p>已经有账号啦~快去<em @click="changelogRegIsShow">登录</em>吧</p>
      </div>
    </div>
  </div>
</template>

<script>
import { mapState } from "vuex";
import { reqRegister, reqInitLogin } from "@/api/index";

export default {
  name: "TicklerLoginRegMask",

  data() {
    return {
      logRegIsShow: true,
      username: "",
      password: undefined,
      regTest: [false, false, false],
    };
  },
  inject:['reload'],
  computed: {
    ...mapState(["lgoRegMaskIsShow"]),
  },
  mounted() {
    this.$bus.$on("changeLogRegMask", this.changelogRegIsShow);
  },

  methods: {
    // 正则检测密码
    testPassword(type) {
      let reg = /^\d{6,18}$/;
      if (!reg.test(this.password)) {
        // 当正则为false
        this.$refs.password.style.color = "red";
        this.$refs.password.style.borderBottom = "2px solid red";
        this.$refs.password.nextElementSibling.style.display = "block";
        this.$refs.btn.disabled = true;
        this.$refs.btn.style.backgroundColor = "#ccc";
        this.regTest.splice(1, 1, false);
      } else {
        // 当正则为true
        this.$refs.password.style.color = "#000";
        this.$refs.password.style.borderBottom = "2px solid rgb(51, 118, 247)";
        this.$refs.password.nextElementSibling.style.display = "none";
        this.regTest.splice(1, 1, true);
        this.releaseBtn(type);
      }
    },
    // 正则检测重复密码
    testPassword1() {
      let reg = /^\d{6,18}$/;
      if (
        !(
          reg.test(this.$refs.password1.value) &&
          this.$refs.password1.value == this.password
        )
      ) {
        // 当正则为false
        this.$refs.password1.style.color = "red";
        this.$refs.password1.style.borderBottom = "2px solid red";
        this.$refs.password1.nextElementSibling.style.display = "block";
        this.$refs.btn.disabled = true;
        this.$refs.btn.style.backgroundColor = "#ccc";
        this.regTest.splice(2, 1, false);
      } else {
        // 当正则为true
        this.$refs.password1.style.color = "#000";
        this.$refs.password1.style.borderBottom = "2px solid rgb(51, 118, 247)";
        this.$refs.password1.nextElementSibling.style.display = "none";
        this.regTest.splice(2, 1, true);
        this.releaseBtn(1);
      }
    },
    // 正则检测用户名
    testUserName(type) {
      let reg = /^[a-zA-Z0-9_-]{4,16}$/;
      if (!reg.test(this.username)) {
        // 当正则为false
        this.$refs.username.style.color = "red";
        this.$refs.username.style.borderBottom = "2px solid red";
        this.$refs.username.nextElementSibling.style.display = "block";
        this.$refs.btn.disabled = true;
        this.$refs.btn.style.backgroundColor = "#ccc";
        this.regTest.splice(0, 1, false);
      } else {
        // 当正则为true
        this.$refs.username.style.color = "#000";
        this.$refs.username.style.borderBottom = "2px solid rgb(51, 118, 247)";
        this.$refs.username.nextElementSibling.style.display = "none";
        this.regTest.splice(0, 1, true);
        this.releaseBtn(type);
      }
    },
    // 判断是否都为T  若都为T可以点击按钮
    releaseBtn(type) {
      // 登录为0 注册1
      if (type) {
        // 注册
        // 数组为三位且都为t
        if (this.regTest[0] && this.regTest[1] && this.regTest[2]) {
          this.$refs.btn.disabled = false;
          this.$refs.btn.style.backgroundColor = "rgb(51, 118, 247)";
        } else {
          this.$refs.btn.disabled = true;
          this.$refs.btn.style.backgroundColor = "#ccc";
        }
      } else {
        if (this.regTest[0] && this.regTest[1]) {
          this.$refs.btn.disabled = false;
          this.$refs.btn.style.backgroundColor = "rgb(51, 118, 247)";
        } else {
          this.$refs.btn.disabled = true;
          this.$refs.btn.style.backgroundColor = "#ccc";
        }
      }
    },
    // 根据设置页判断显示登录或注册  合并遮罩层右下点击切换登陆注册状态
    changelogRegIsShow(type) {
      this.username = "";
      this.password = "";

      if (type == 1 || type == 0) {
        this.logRegIsShow = type;
      } else {
        this.logRegIsShow = !this.logRegIsShow;
        this.$refs.username.style.color = "#000";
        this.$refs.username.style.borderBottom = "2px solid rgb(51, 118, 247)";
        this.$refs.username.nextElementSibling.style.display = "none";
        this.$refs.password.style.color = "#000";
        this.$refs.password.style.borderBottom = "2px solid rgb(51, 118, 247)";
        this.$refs.password.nextElementSibling.style.display = "none";
      }
    },
    // 派发store隐藏遮罩层
    changeLogRegMask() {
      this.$store.dispatch("changelogMaskShow", false);
    },
    // 点击登录
    async getLoginMsg() {
      try {
        let data = {
          username: this.username,
          password: this.password,
        };
        let res = await reqInitLogin(data);

        // 当前res请求成功
        if (res.status == 200) {
          // 本地化存储token
          this.$bus.$emit("alertMsg", "登录成功");
          this.$store.dispatch("storageToken", res.data.token);

          // 清除数据
          this.username = "";
          this.password = "";
          // 隐藏遮罩层
          this.$store.dispatch("changelogMaskShow", false);
          this.reload()
        }
      } catch (error) {
        console.log(error.message);
      }
    },
    // 点击注册
    async getRegister() {
      try {
        let data = {
          username: this.username,
          password: this.password,
        };
        console.log(data);
        let res = await reqRegister(data);
        console.log(res);
        // 当前res请求成功
        if (res.status == 200) {
          // 本地化存储token
          // 清除数据
          this.username = "";
          this.password = "";
          this.$refs.password1.value = "";
          this.$bus.$emit("alertMsg", "注册成功");
          // 隐藏遮罩层
          this.$store.dispatch("changelogMaskShow", false);
        }
      } catch (error) {
          this.$bus.$emit("alertMsg", "error.message");
      }
    },
  },
};
</script>

<style lang="less" scoped>
// 登录注册遮罩层
.login-mask {
  position: absolute;
  top: 0;
  right: 0;
  bottom: 0;
  left: 0;
  background: rgba(136, 136, 136, 0.3);
  z-index: 9999;

  .mask-box {
    display: flex;
    position: absolute;
    top: 50%;
    left: 50%;
    transform: translate(-50%, -50%);
    width: 60%;
    background-color: #fff;
    border-radius: 20px;
    box-sizing: border-box;
    overflow: hidden;
    background-color: rgb(226, 234, 246);

    .pic {
      width: 400px;
      height: 400px;

      img {
        width: 100%;
      }
    }

    .login-body,
    .register-body {
      flex: 1;
      float: left;
      margin-left: 10%;

      h1 {
        font-size: 25px;
        color: rgb(51, 118, 247);
        height: 100px;
        line-height: 100px;

        span {
          cursor: pointer;
          font-size: 12px;
          float: right;
          margin-right: 80px;
          color: rgb(158, 158, 158);
        }
      }

      ul {
        margin-top: 20px;
        margin-left: 20px;

        li {
          position: relative;
          font-size: 14px;
          height: 60px;
          em {
            display: inline-block;
            width: 0.5em;
          }

          input {
            width: 60%;
            padding: 0 5px;
            height: 30px;
            outline: none;
            border: none;
            border-bottom: 2px solid rgb(51, 118, 247);
            background-color: rgb(226, 234, 246);
            margin-left: 10px;
          }

          p {
            display: none;
            position: absolute;
            left: 20%;
            font-size: 12px;
            color: red;
          }
        }
      }
      button {
        width: 200px;
        height: 40px;
        border-radius: 20px;
        margin-left: 20%;
        color: #fff;
        border: none;
        background-color: rgb(51, 118, 247);
      }
      & > p {
        float: right;
        color: rgb(110, 110, 110);
        margin-top: 10px;
        margin-right: 60px;

        em {
          cursor: pointer;
          color: #3476f4;
        }
      }
    }
  }
}
</style>