<template>
  <div class="calendar">
    <!-- 日历容器 -->
    <ul v-show="isShow">
      {{
        weekArray
      }}
      <li v-for="(day, index) in week" :key="day.dater">
        <h3>{{ getWeekDay(index) }}</h3>
        <i
          :class="{ active: isActive == day.dater }"
          @click="
            changeList(
              1,
              day.dater,
              day.num.split('/')[1],
              day.num.split('/')[2]
            )
          "
          >{{ day.num.split("/")[2] }}</i
        >
      </li>
    </ul>
    <div v-show="!isShow" class="calendartitle">
      <em class="iconfont icon-zuoce" @click="changeMonth(-1)"></em>
      <h1>{{ nowyear }}年{{ nowmonth }}月</h1>
      <em class="iconfont icon-youce" @click="changeMonth(1)"></em>
    </div>
    <ul v-show="!isShow">
      <li v-for="(item, index) in monthArray" :key="item.dater">
        <h3 v-show="index < 7">{{ getWeekDay(index) }}</h3>
        <i
          :class="{
            active: isActive == item.dater,
            forbid: !(nowmonth == item.num.split('/')[1]),
          }"
          @click="
            changeList(
              0,
              item.dater,
              item.num.split('/')[1],
              item.num.split('/')[2],
              nowmonth
            )
          "
          >{{ item.num.split("/")[2] }}</i
        >
      </li>
    </ul>
    <!-- 下拉栏 -->
    <span @click="toBigCalendar($event)"></span>
    <!-- 查看数组输出 -->
    <i>{{ comMonthArr }}</i>
  </div>
</template>

<script>
export default {
  name: "MyCalendar",

  data() {
    return {
      // 这两个值不能被修改
      // 存储当前天
      nowDay: undefined,
      nowdater: 0,
      // 当前为周几
      // 是否被选中
      isActive: 0,
      isShow: true,
      // 存储当前月
      nowmonth: -1,
      nowyear: 0,
      week: [],
      monthArray: [],
      monthly: [1, 3, 5, 7, 8, 10, 12],
    };
  },
  mounted() {
    this.getNowDay();
    this.$bus.$emit("getNowDater", this.isActive);
    // 三级路由挂载晚等页面DOM完成再传
    this.$nextTick(()=>{
    this.$bus.$emit("getorderDater", this.isActive);
    this.$bus.$emit("getcanvasDater", this.isActive);
    this.$store.commit('STORAGENOWDAY',`${this.nowyear}.${this.nowmonth}.${this.nowDay}`)
    });
  },
  methods: {
    // 计算当前天为周几 存储到数组 并筛选或剪切留下周一至周日
    getNowDay() {
      let now = new Date();
      //   当前日
      let day = now.getDate();
      //   当前周几
      let month = now.getMonth() + 1;
      let year = now.getFullYear();
      // 初始化
      this.nowDay = day;
      this.nowmonth = month;
      this.nowyear = year;
      this.isActive = Number(`${year}${month}${day}`);
    },
    // 改变选中状态
    changeList(type, num, btnmonth, btnday, nowmonth) {
      if (type == 1) {
        this.nowDay = Number(btnday);
        this.nowmonth = Number(btnmonth);
        this.$bus.$emit("getNowDater",num);
        this.$bus.$emit("getorderDater", num);
        this.$bus.$emit("getcanvasDater", num);
        // 判断为当前月的日子走里面逻辑
        // 控制样式
        this.isActive = num;
      } else {
        if (btnmonth == nowmonth) {
          this.nowDay = Number(btnday);
          this.nowmonth = Number(btnmonth);
          this.$bus.$emit("getNowDater",num);
          this.$bus.$emit("getorderDater", num);
          this.$bus.$emit("getcanvasDater", num);
          // 判断为当前月的日子走里面逻辑
          // 控制样式
          this.isActive = num;
          this.toBigCalendar();
        }
      }
    },

    // 封装方法遍历创建数组
    createMonthArray(...arr) {
      // 赋值解构
      let [startyear, startmonth, startday, endday, row] = arr;
      // 防止多余操作
      if (startyear) {
        let Arr = [];
        for (let i = 1; i <= row * 7; i++) {
          let obj = {
            num: `${startyear}/${startmonth}/${startday}`,
            dater: Number(`${startyear}${startmonth}${startday}`),
          };
          Arr.push(obj);
          startday++;
          // 必定有-1 0 两个月   可能-1 0 1三个月   一整套判断月  只对月份进行判断 超过当前月天数 月+1   月超过12 年+1
          if (
            ((startyear % 4 == 0 && startyear % 100 != 0) ||
              startyear % 400 == 0) &&
            startmonth == 2
          ) {
            // 29
            if (startday > 29) {
              startday = 1;
              startmonth++;
              if (startmonth > 12) {
                startmonth = 1;
                startyear++;
              }
            }
          } else {
            if (startmonth == 2) {
              // 28
              if (startday > 28) {
                startday = 1;
                startmonth++;
                if (startmonth > 12) {
                  startmonth = 1;
                  startyear++;
                }
              }
            } else if (this.monthly.includes(startmonth)) {
              // 31
              if (startday > 31) {
                startday = 1;
                startmonth++;
                if (startmonth > 12) {
                  startmonth = 1;
                  startyear++;
                }
              }
            } else {
              // 30
              if (startday > 30) {
                startday = 1;
                startmonth++;
                if (startmonth > 12) {
                  startmonth = 1;
                  startyear++;
                }
              }
            }
          }
        }

        this.monthArray = Arr;
      }
    },

    //切换大小日历
    toBigCalendar() {
      this.isShow = !this.isShow;
    },
    // 获取周1-7
    getWeekDay(index) {
      switch (index) {
        case 0:
          return "周一";
          break;
        case 1:
          return "周二";
          break;
        case 2:
          return "周三";
          break;
        case 3:
          return "周四";
          break;
        case 4:
          return "周五";
          break;
        case 5:
          return "周六";
          break;
        case 6:
          return "周日  ";
          break;
      }
    },
    // 按钮改变月份
    changeMonth(num) {
      this.nowmonth += num;
      this.nowDay = 1;
      if (this.nowmonth <= 0) {
        this.nowyear -= 1;
        this.nowmonth = 12;
      }

      if (this.nowmonth >= 13) {
        this.nowyear += 1;
        this.nowmonth = 1;
      }

      this.isActive = Number(`${this.nowyear}${this.nowmonth}1`);
    },
  },

  computed: {
    // 存储处理的一周日期
    weekArray() {
      // 获取 当前年 当前月 当前天 获取当前时间戳  当前周
      let year = this.nowyear;
      let month = this.nowmonth;
      let nowday = this.nowDay;
      const now = new Date(`${year}/${month}/${nowday}`);
      let nowWeek = now.getDay();
      if (nowday) {
        // 初始化开始天数
        let startday;
        // 初始化结束天数
        let endday;
        // 作为判断条件
        let arr = this.monthly;
        // 进行判断当前是否为周日
        if (nowWeek != 0) {
          // 计算起始日
          startday = nowday + 7 - nowWeek - 6;
          endday = nowday + 7 - nowWeek;
          if (startday <= 0) {
            month--;
            if (month == 0) {
              year--;
            }
            // 判断当前年月是否为特殊年月分别处理
            if ((year % 4 == 0 && year % 100 != 0) || year % 400 == 0) {
              if (month == 2) {
                startday = 29 + startday;
              } else {
                if (arr.includes(month)) {
                  // 上一月为31
                  startday = 31 + startday;
                } else {
                  // 上一月为30
                  startday = 30 + startday;
                }
              }
            } else {
              if (month == 2) {
                startday = 28 + startday;
              } else {
                if (arr.includes(month)) {
                  // 上一月为31
                  startday = 31 + startday;
                } else {
                  // 上一月为30
                  startday = 30 + startday;
                }
              }
            }
          }
          // 当天为周日逻辑
        } else {
          // 为周日则当天向前-6 既为开始天数
          startday = nowday - 6;
          endday = nowday;

          if (startday <= 0) {
            month--;
            if (month == 0) {
              year--;
            }
            // 判断当前年月是否为特殊年月分别处理
            if ((year % 4 == 0 && year % 100 != 0) || year % 400 == 0) {
              if (month == 2) {
                startday = 29 + startday;
              } else {
                if (arr.includes(month)) {
                  // 上一月为31
                  startday = 31 + startday;
                } else {
                  // 上一月为30
                  startday = 30 + startday;
                }
              }
            } else {
              if (month == 2) {
                startday = 28 + startday;
              } else {
                if (arr.includes(month)) {
                  // 上一月为31
                  startday = 31 + startday;
                  console.log(startday);
                } else {
                  // 上一月为30
                  startday = 30 + startday;
                }
              }
            }
          }
        }
        let weekarr = [];
        // 遍历存储在data的week  给里面存储当周的值
        for (let i = 1; i <= 7; i++) {
          // 存储进对象
          let obj = {
            num: `${year}/${month}/${startday}`,
            dater: Number(`${year}${month}${startday}`),
          };
          weekarr.push(obj);
          startday++;
          // 判断是否闰年
          if ((year % 4 == 0 && year % 100 != 0) || year % 400 == 0) {
            // 判断是否二月
            if (month == 2) {
              if (startday > 29) {
                startday = 1;
                month++;
              }
            } else {
              if (arr.includes(month)) {
                // 判断是否31天
                if (startday > 31) {
                  startday = 1;
                  month++;
                  if (month > 12) {
                    year++;
                    month = 1;
                  }
                }
              } else {
                // 判断是否超过30天
                if (startday > 30) {
                  startday = 1;
                  month++;
                }
              }
            }
          } else {
            // 非闰年
            if (month == 2) {
              // 判断是否28天
              if (startday > 28) {
                startday = 1;
                month++;
              }
            } else {
              // 非闰年正常月份
              if (arr.includes(month)) {
                // 判断是否31天
                if (startday > 31) {
                  startday = 1;
                  month++;
                  if (month > 12) {
                    year++;
                    month = 1;
                  }
                }
              } else {
                // 判断是否超过30天
                if (startday > 30) {
                  startday = 1;
                  month++;
                }
              }
            }
          }
        }
        this.week = weekarr;
      }
    },

    // 获取月初一号为周几  判断当前月有多少天
    comMonthArr: {
      get() {
        // 获取当前月  修改了初始值 使其无效
        let month = this.nowmonth;
        // 初始化始终月
        let startmonth;
        let endmonth;
        // 存储当前年
        let year = this.nowyear;
        // 初始化始终年
        let startyear = year;
        let endyear;
        let startday;
        let endday;
        // 存储当月第一天的时间戳
        const now = new Date(`${year}/${month}/1`);
        this.nowdater = Number(`${year}${month}${this.nowDay}`);
        // 存储当月月末时间戳
        let end;
        // 获取当前月末时间戳
        if ((year % 4 == 0 && year % 100 != 0) || year % 400 == 0) {
          if (month == 2) {
            end = new Date(`${year}/${month}/29`);
          } else if (this.monthly.includes(month)) {
            end = new Date(`${year}/${month}/31`);
          } else {
            end = new Date(`${year}/${month}/30`);
          }
        } else {
          if (month == 2) {
            end = new Date(`${year}/${month}/28`);
          } else if (this.monthly.includes(month)) {
            end = new Date(`${year}/${month}/31`);
          } else {
            end = new Date(`${year}/${month}/30`);
          }
        }
        // 获取当月周一为周几
        let startMonthWeekDay = now.getDay();
        // 获取月末为周几
        let endMonthWeekDay = end.getDay();
        // 初始化当前月第一天
        let currentMonthStart = undefined;
        let monArry = [];
        // 起始天 是否为周日
        if (startMonthWeekDay != 0) {
          // 此时为负值
          startday = 1 + 1 - startMonthWeekDay;
          if (startday <= 0) {
            startmonth = month - 1;
            // 判断是否减一年
            if (startmonth == 0) {
              startyear = year - 1;
              startmonth = 12;
            }
            // 对起始时间进行处理
            if (
              (startyear % 4 == 0 && startyear % 100 != 0) ||
              startyear % 400 == 0
            ) {
              if (startmonth == 2) {
                startday = 29 + startday;
              } else {
                if (this.monthly.includes(startmonth)) {
                  // 上一月为31
                  startday = 31 + startday;
                } else {
                  // 上一月为30
                  startday = 30 + startday;
                }
              }
            } else {
              if (startmonth == 2) {
                startday = 28 + startday;
              } else {
                if (this.monthly.includes(startmonth)) {
                  // 上一月为31
                  startday = 31 + startday;
                } else {
                  // 上一月为30
                  startday = 30 + startday;
                }
              }
            }
          } else {
            startmonth = month;
          }
        } else {
          startday = -5;
          startmonth = month - 1;
          console.log(startmonth, month);
          if (startmonth == 0) {
            startyear = year - 1;
            startmonth = 12;
          }
          // 对起始时间进行处理
          if (
            (startyear % 4 == 0 && startyear % 100 != 0) ||
            startyear % 400 == 0
          ) {
            if (startmonth == 2) {
              startday = 29 + startday;
            } else {
              if (this.monthly.includes(startmonth)) {
                // 上一月为31
                startday = 31 + startday;
              } else {
                // 上一月为30
                startday = 30 + startday;
              }
            }
          } else {
            if (startmonth == 2) {
              startday = 28 + startday;
            } else {
              if (this.monthly.includes(startmonth)) {
                // 上一月为31
                startday = 31 + startday;
              } else {
                // 上一月为30
                startday = 30 + startday;
              }
            }
          }
        }

        //判断 当前二月一号日期为1   为平年
        if (
          startMonthWeekDay == 1 &&
          startmonth == 2 &&
          !((year % 4 == 0 && year % 100 != 0) || year % 400 == 0)
        ) {
          // for存储数组
          // 4行情况
          for (let i = 1; i <= 28; i++) {
            let obj = {
              num: `${startyear}/${startmonth}/${i}`,
              dater: Number(`${startyear}${startmonth}${i}`),
            };
            monArry.push(obj);
          }
          this.monthArray = monArry;
        } else if (
          (startMonthWeekDay == 6 && this.monthly.includes(startmonth)) ||
          (startMonthWeekDay == 0 && startmonth != 2)
        ) {
          // 六行情况
          for (let i = 1; i <= 42; i++) {
            // endday为31
            if (this.monthly.includes(month)) {
              endday = 31;
              this.createMonthArray(startyear, startmonth, startday, endday, 6);
              // endday为30
            } else {
              endday = 30;
              this.createMonthArray(startyear, startmonth, startday, endday, 6);
            }
          }
        } else {
          //五行情况
          if (month != 2) {
            if (this.monthly.includes(month)) {
              endday = 31;
              // endday为30
              this.createMonthArray(startyear, startmonth, startday, endday, 5);
            } else {
              endday = 30;
              this.createMonthArray(startyear, startmonth, startday, endday, 5);
            }
          } else {
            if ((year % 4 == 0 && year % 100 != 0) || year % 400 == 0) {
              endday = 29;
              this.createMonthArray(startyear, startmonth, startday, endday, 5);
            } else {
              endday = 28;
              this.createMonthArray(startyear, startmonth, startday, endday, 5);
            }
          }
        }
      },
    },
  },
};
</script>

<style lang="less" scoped>
.calendar {
  z-index: 999;
  padding: 10px 0;

  .calendartitle {
    text-align: center;
    font-size: 20px;
    font-weight: 700;
    height: 70px;
    line-height: 50px;
    color: rgb(136, 136, 136);

    h1 {
      vertical-align: middle;
      margin: 0 50px;
      display: inline-block;
    }

    em {
      vertical-align: middle;
      font-size: 28px;
    }
  }

  ul {
    display: flex;
    flex-wrap: wrap;
    justify-content: space-around;

    li {
      position: relative;
      width: 14%;
      margin-bottom: 25px;
      text-align: center;
      i {
        display: inline-block;
        width: 46px;
        height: 46px;
        font-size: 18px;
        font-weight: 700;
        cursor: pointer;
        border-radius: 50%;
        line-height: 46px;
      }

      h3 {
        line-height: 10px;
        height: 10px;
        font-size: 12px;
        color: #85878c;
        margin-bottom: 15px;
      }
    }
  }

  // 下拉栏
  span {
    position: absolute;
    bottom: 0;
    width: 100%;
    height: 25px;

    &::after {
      position: absolute;
      top: 50%;
      left: 50%;
      transform: translate(-50%, -50%);
      content: "";
      width: 40px;
      height: 4px;
      background-color: #ccc;
      border-radius: 2px;
    }
  }
}

// 选中状态
.active {
  color: #fff;
  background-color: #3476f4 !important;

  &::after {
    position: absolute;
    bottom: -10px;
    left: 50%;
    transform: translateX(-50%);
    content: "";
    width: 6px;
    height: 6px;
    background: #83c0ff;
    border-radius: 50%;
  }
}

// 禁用状态
.forbid {
  cursor: not-allowed !important;
  color: #fff;
  background-color: rgb(136, 136, 136);
}
</style>