export const reqInitLogin = (datas)=> request({url:'http://www.liulongbin.top:3007/api/login',method:'post',data:qs.stringify(datas)})

// 接收信息
export const reqInitUserMessage = ()=>request({url:`http://big-event-api-t.itheima.net/my/userinfo`,headers:{Authorization:localStorage.getItem("TOKEN"),method:'get'}})

